import { Injectable } from '@angular/core';
import { StateMachine, NgStatemachine, State, NgState, RenderState,  NgRenderState, Action, NgAction } from '@farris/devkit';

@Injectable()
@NgStatemachine()
class FlatFuncTreeStatemachine extends StateMachine {

  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */

  /**
   * 
   */
  @NgState({ initialState: true })
  viewState: State;

  /**
   * 
   */
  @NgState()
  addState: State;

  /**
   * 
   */
  @NgState()
  editState: State;


  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAdd: RenderState;

    /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAddChild: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canEdit: RenderState;


  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canCancel: RenderState;


  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canDelete: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canSave: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canSwitch: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canInput: RenderState;


  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */
  @NgAction({transitTo: 'addState' })
  add: Action;

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'viewState' })
  view: Action;
}

export { FlatFuncTreeStatemachine };



